import { high } from 'nc-lightapp-front';

const { Refer } = high;

export default function (props = {}) {
    var conf = {
        multiLang: {
            domainName: 'hpf',
            currentLocale: 'zh-CN',
            moduleId: 'M0Z10302',
        },
        isCacheable: false,
        refType: 'grid',
        refName: '参照信息',/* 国际化处理： 参照名*/
        placeholder: '参照信息',/* 国际化处理： 参照名*/
        refCode: 'refer.hpfrefs.RefInfoGridRef',//不知道是什么意思
        queryGridUrl: '/nccloud/hpf/refinfo/GridReferQuery.do',
        columnConfig: [{ name: ['编码', '名称'], code: ['code', 'name'] }],/* 国际化处理： 编码,名称*/
        isMultiSelectedEnabled: false,
        isHasDisabledData: false
    };

    return <Refer {...conf} {...props} />
}